import React, { Component, lazy, Suspense } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import { getUUID } from "../actions/fakturAction";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import { reset } from "redux-form";
import {
  finishSend,
  getBruto,
  getGross,
  getNetto,
  getPkg,
  getQty,
  getRp,
  progressSend,
} from "../actions/LogicAction";
import { getTableTotalanTimbangan } from "../actions/TotalanTimbanganAction";
import Axios from "axios";
import PrintNota from "../Stocking/Component/PrintNota";
import HeaderCard from "../HeaderCard";
import Skeleton from "react-loading-skeleton";
import { showModal } from "../actions/ModalAction";

const FormModalTimbangan = lazy(() =>
  import("./Components/FormModalTimbangan")
);
const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);
const maptostate = (state) => {
  return {
    totalanTimbangan: state.provinsi.totalanTimbangan,
    noFaktur: state.provinsi.noFaktur,
    listTotalanTimbangan: state.provinsi.listTotalanTimbangan,
    sumBruto: state.perhitungan.sumBruto,
    sumNetto: state.perhitungan.sumNetto,
    sumGross: state.perhitungan.sumGross,
    sumPkg: state.perhitungan.sumPkg,
    sumQty: state.perhitungan.sumQty,
    sumRp: state.perhitungan.sumRp,
    onSend: state.provinsi.onSend,
  };
};
const { SearchBar } = Search;
const server = process.env.REACT_APP_BACKEND_URL;
const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class TotalanTimbangan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      jenisBarang: "RONGSOK",
      listLokasi: [],
      columns: [
        {
          dataField: "kodeJenis",
          text: "Kode",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadarCetak",
          text: "Kadar",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "gross",
          text: "Gross (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "pkg",
          text: "Pkg (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "bruto",
          text: "Berat (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadarTransaksi",
          text: "Harga",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "netto",
          text: "Netto (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "rp",
          text: "Ongkos (Rp)",
          sort: true,
          headerStyle: () => {
            return { width: "25%" };
          },
        },
      ],
      data: [
        {
          tanggal: "2020-11-10",
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getTableTotalanTimbangan());
    this.props.dispatch(getUUID());
    this.props.dispatch(getBruto("totalanTimbangan"));
    this.props.dispatch(getNetto("totalanTimbangan"));
    this.props.dispatch(getPkg("totalanTimbangan"));
    this.props.dispatch(getGross("totalanTimbangan"));
    this.props.dispatch(getRp("totalanTimbangan"));
    this.props.dispatch(getQty("totalanTimbangan"));
    Axios.get(server + "storages2?tipeData=LOKASI")
      .then((res) =>
        this.setState({
          listLokasi: res.data,
        })
      )
      .catch((err) => console.log(err));
  }

  handleSubmit(data) {
    var isi = data.kodeBarang
      ? data.kodeBarang
      : "DEFAULT|DEFUALT|DEFAULT|DEFAULT";
    var explode = isi.split("|");
    var simpan = {
      nettoDiscGlobalExt: 0,
      gross: parseFloat(data.gross),
      kodeJenis: explode[0],
      pkg: parseFloat(data.pkg),
      noBarcode: "-",
      qty: 1,
      k: 0,
      kadarCetak: explode[1],
      kodeKategori: "-",
      p: 0,
      nettoDiscGlobal: 0,
      discGlobalPr: 0,
      discGlobalGr: 0,
      kadarTransaksi: parseFloat(data.kadarTransaksi),
      noFaktur: data.noFaktur,
      bruto: parseFloat(data.gross) - parseFloat(data.pkg),
      netto: parseFloat(data.netto),
      rp: parseFloat(data.rp),
      keterangan: data.keterangan,
    };
    var array = JSON.parse(localStorage.getItem("totalanTimbangan")) || [];
    array.push(simpan);
    localStorage.setItem("totalanTimbangan", JSON.stringify(array));
    Swal.fire({
      position: "top-right",
      icon: "success",
      text: "Transaksi Berhasil",
      timer: 1500,
      showConfirmButton: false,
    });
    this.props.dispatch(reset("FormModalTimbangan"));
    this.props.dispatch(getTableTotalanTimbangan());
  }
  simpanTotalanTimbang() {
    if (this.state.tanggal === undefined) {
      Swal.fire({
        position: "top-end",
        icon: "error",
        text: "Silahkan Isi Tanggal",
        showConfirmButton: false,
        timer: 1500,
      });
      return false;
    } else if (this.state.lokasi === undefined) {
      Swal.fire({
        position: "top-end",
        icon: "error",
        text: "Silahkan Isi Lokasi",
        showConfirmButton: false,
        timer: 1500,
      });
      return false;
    } else if (localStorage.getItem("totalanTimbangan") === null) {
      Swal.fire({
        position: "top-end",
        icon: "error",
        text: "Data Masih Kosong, Silahkan tambah data",
        showConfirmButton: false,
        timer: 1500,
      });
      return false;
    }
    var simpan = {
      tipeFaktur: this.state.jenisBarang,
      noFaktur: this.props.noFaktur,
      noBon: this.props.noFaktur,
      tanggal: this.state.tanggal,
      noValid: "-",
      kodeLokasi: this.state.kodeLokasi,
      barangDetails: JSON.parse(localStorage.getItem("totalanTimbangan")),
      footerItem: {
        totalP: 0,
        totalGross: this.props.sumGross,
        totalpkg: this.props.sumPkg,
        totalQty: this.props.sumQty,
        totalK: 0,
        totalRp: this.props.sumRp,
        totalBruto: this.props.sumBruto,
        totalNetto: this.props.sumNetto,
        totalNettoExt: 0,
      },
    };
    this.props.dispatch(progressSend());
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("totalanTimbangan")) || [];
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        i++,
        ticket.kodeJenis,
        ticket.gross,
        ticket.pkg,
        ticket.bruto,
        ticket.kadarTransaksi,
        ticket.netto,
        ticket.rp,
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    console.log(tableRows);
    var columTable = [
      "No",
      "Kode Barang",
      "Gross (Gr)",
      "Pkg (Gr)",
      "Bruto (Gr)",
      "Harga (%)",
      "Netto (Gr)",
      "Ongkos (Rp)",
    ];
    // AKHIR INISIALISASI AUTOTABLE

    Axios.post(serverNonMaster + "item/dc/totalantimbang", simpan)
      .then(() =>
        Swal.fire({
          position: "top-right",
          icon: "success",
          text: "Transaksi Berhasil",
          timer: 1500,
          showConfirmButton: false,
        })
          .then(() => this.props.dispatch(finishSend()))
          .then(() =>
            PrintNota(
              "Tanggal",
              this.state.tanggal,
              "Lokasi",
              this.state.tanggal,
              "No Timbang",
              this.props.noFaktur,
              "",
              "",
              "",
              "",
              "",
              columTable,
              "STOCK OPNAME TIMBANG SALES",
              tableRows
            )
          )
          .then(() => localStorage.removeItem("totalanTimbangan"))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() => this.props.dispatch(getUUID()))
          .then(() => this.props.dispatch(getTableTotalanTimbangan()))
          .then(() =>
            this.setState({
              tanggal: "",
              lokasi: "",
            })
          )
      )
      .catch((err) =>
        Swal.fire({
          position: "top-right",
          icon: "error",
          text:
            "Sepertinya ada kesalahan di server, mohon periksa koneksi dan ulangi transaksi",
          timer: 1500,
          showConfirmButton: false,
        }).then(() => console.log(err))
      );
  }
  changeInput = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  render() {
    return (
      <div className="container-fluid">
        <div className="card">
          <HeaderCard title="Totalan Timbangan" />
          <div className="card-body">
            <div className="row">
              <div className="col-lg-4 mb-3">
                <label htmlFor="">Jenis Barang</label>
                <select
                  name="jenisBarang"
                  id="JenisBarang"
                  className="form-control"
                  onChange={(e) =>
                    this.setState({
                      jenisBarang: e.target.value,
                    })
                  }
                >
                  <option value="RONGSOK" key="RONGSOK">
                    RONGSOK
                  </option>
                  <option value="PERHIASAN" key="PERHIASAN">
                    PERHIASAN
                  </option>
                </select>
              </div>
              <div className="col-lg-8 mb-3"></div>
              <div className="col-lg-4">
                <div className="form-group">
                  <label htmlFor="">Tanggal</label>
                  <input
                    type="date"
                    name="tanggal"
                    label="tanggal"
                    className="form-control"
                    onChange={this.changeInput("tanggal")}
                  />
                </div>
              </div>
              <div className="col-lg-4">
                <div className="form-group">
                  <label htmlFor="">Lokasi</label>
                  <select
                    name="lokasi"
                    label="lokasi"
                    className="form-control"
                    onChange={this.changeInput("lokasi")}
                  >
                    <option value="DEFAULT" key="DEFAULT">
                      SILAHKAN PILIH
                    </option>
                    {this.state.listLokasi.map((list) => (
                      <option value={list.kodeLokasi} key={list.kodeLokasi}>
                        {list.namaLokasi}
                      </option>
                    ))}
                  </select>
                </div>
              </div>
              <div className="col-lg-12 mt-3 mb-3">
                <div className="row">
                  <div className="col-lg-6">
                    <div className="text-left">
                      <button
                        className="btn btn-primary"
                        disabled={this.props.onSend}
                        onClick={() => this.simpanTotalanTimbang()}
                      >
                        {this.props.onSend ? (
                          <span
                            className="spinner-border spinner-border-sm mr-3"
                            aria-hidden="true"
                          ></span>
                        ) : null}
                        Simpan
                        <i className="fa fa-paper-plane ml-3"></i>
                      </button>
                    </div>
                  </div>
                  <div className="col-lg-6">
                    <div className="text-right">
                      <button
                        className="btn btn-primary"
                        onClick={() => this.props.dispatch(showModal())}
                      >
                        Tambah Data
                        <i className="fa fa-plus ml-3"></i>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-lg-12">
                {this.props.totalanTimbangan ? (
                  <ToolkitProvider
                    keyField="kodeKelompok"
                    data={this.props.totalanTimbangan}
                    columns={this.state.columns}
                    search
                  >
                    {(props) => (
                      <div className="form-group">
                        <div className="text-right mt-5 mb-3">
                          <SearchBar
                            {...props.searchProps}
                            className="form-control"
                            placeholder="Tuliskan Kode / Nama"
                            style={{ margin: 0, top: "50%" }}
                          />
                        </div>
                        <BootstrapTable
                          {...props.baseProps}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                          pagination={paginationFactory()}
                        />
                      </div>
                    )}
                  </ToolkitProvider>
                ) : (
                  <BootstrapTable
                    keyField="kodeKelompok"
                    data={[]}
                    columns={this.state.columns}
                    bordered={true}
                    headerClasses="header-table"
                    noDataIndication="Tidak Ada Data"
                  />
                )}
              </div>
            </div>
          </div>
        </div>
        <ModalMasterData
          content={
            <Suspense
              fallback={<Skeleton width={"100%"} height={50} count={5} />}
            >
              <FormModalTimbangan
                noFaktur={this.props.noFaktur}
                jenisBarang={this.state.jenisBarang}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            </Suspense>
          }
          title=""
        />
      </div>
    );
  }
}

export default connect(maptostate, null)(TotalanTimbangan);
